@import '../../../style/default.scss';
@import '../../../style/theme.scss';

##{$htht-prefix}-header {
  display: flex;
  justify-content: space-between;
  // max-width: 12rem;
  // margin: 0 1rem;
  box-sizing: border-box;
  padding: 0 0.4rem;
  z-index: 2;
  height: $header-height;
  border-bottom: 1px solid #f0f0f0;
  box-shadow: 0 2px 8px #f0f1f2;
  position: relative;

  #logo {
    a {
      height: 100%;
      display: flex;
      align-items: center;

      img {
        width: 0.35rem;
        height: 0.35rem;
        margin-right: 0.16rem;
      }

      h1 {
        font-size: 0.18rem;
        color: $font-gray-color;
      }
    }
  }

  #nav {
    ul {
      display: flex;
      height: 100%;
      align-items: center;

      li {
        height: 100%;

        &.active,
        &:hover {
          a {
            border-top-color: $main-color;
            color: $main-color;
          }
        }

        a {
          display: flex;
          align-items: center;
          height: 100%;
          color: $font-gray-color;
          padding: 0 0.2rem;
          border-top: 0.02rem solid transparent;
          padding-top: 0.05rem;
        }
      }
    }
  }
}
